<template>
  <div class="main">
    <Navbar/>
    <el-row>
      <el-col :span="18" class="main-left">
        <StocksDataTable/>
        <DataTabs/>
      </el-col>
      <el-col :span="6">
        <ProdudctStrikePrices/>
        <MyAccount/>
        <Buy/>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import Navbar from '@/components/Navbar'
import StocksDataTable from '@/components/StocksDataTable'
import ProdudctStrikePrices from '@/components/ProdudctStrikePrices'
import MyAccount from '@/components/MyAccount'
import Buy from '@/components/Buy'
import DataTabs from '@/components/DataTabs'

export default {
  name: 'Index',
  components: {
    StocksDataTable,
    Navbar,
    ProdudctStrikePrices,
    MyAccount,
    Buy,
    DataTabs,
  },
}
</script>

<style lang="scss">
@import "~amstock3/amcharts/style.css";
.main {
  .main-left {
    background: #ffffff;
  }
}
</style>



