<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>顯示帳號</span>
    </div>
    <div class="account-item item-odd">
      <span class="item-label">{{ myaccount.yesterDayEquity.label }}</span>：
      <span class="item-value">{{ myaccount.yesterDayEquity.value }}</span>
    </div>
    <div class="account-item">
      <span class="item-label">{{ myaccount.todayIncome.label }}</span>：
      <span class="item-value">{{ myaccount.todayIncome.value }}</span>
    </div>
    <div class="account-item item-odd">
      <span class="item-label">{{ myaccount.remainingPrice.label }}</span>：
      <span class="item-value">{{ myaccount.remainingPrice.value }}</span>
    </div>
    <div class="account-item">
      <span class="item-label">{{ myaccount.forcePrice.label }}</span>：
      <span class="item-value">{{ myaccount.forcePrice.value }}</span>
    </div>
    <div class="account-item item-odd">
      <span class="item-label">{{ myaccount.promisePrise.label }}</span>：
      <span class="item-value">{{ myaccount.promisePrise.value }}</span>
    </div>
    <div class="account-item">
      <span class="item-label">{{ myaccount.totalEquity.label }}</span>：
      <span class="item-value">{{ myaccount.totalEquity.value }}</span>
    </div>
  </el-card>
</template>

<script>
export default {
  name: 'MyAccount',
  data() {
    return {
      myaccount: {
        yesterDayEquity: {
          label: '昨日權益數',
          value: _.random(1000000, 10000000),
        },
        todayIncome: {
          label: '今日損益',
          value: _.random(1000, 10000),
        },
        remainingPrice: {
          label: '可用餘額',
          value: _.random(1000000, 10000000),
        },
        forcePrice: {
          label: '強平額度',
          value: _.random(1000000, 10000000),
        },
        promisePrise: {
          label: '留倉保證金',
          value: _.random(1000, 10000),
        },
        totalEquity: {
          label: '權益總數',
          value: _.random(1000000, 10000000),
        },
      }
    }
  },
}
</script>

<style lang="scss">
.box-card {
  margin: 15px;
  .el-card__body {
    padding: 0;
    .account-item {
      font-size: 14px;
      padding: 5px;
      &.item-odd {
        background-color: #e8e8e8;
      }
      .item-label {
        display: inline-block;
        width: 30%;
      }
    }
  }
}
</style>



