<template>
  <el-card class="box-card">
    <div slot="header" class="clearfix">
      <span>報價明細 [{{ itemName }}]</span>
    </div>
    <el-table
    :data="transactions"
    border
    height="400">
    <el-table-column
      prop="time"
      label="時間">
    </el-table-column>
    <el-table-column
      prop="price"
      label="價格">
    </el-table-column>
    <el-table-column
      prop="count"
      label="單量">
    </el-table-column>
  </el-table>
  </el-card>
</template>

<script>
export default {
  name: 'ProductStrikePrices',
  data() {
    return {
      itemName: '德國DAX9',
      transactions: []
    }
  },
  mounted() {
    let vm = this
    vm.generateFakeData()
  },
  methods: {
    generateFakeData() {
      let vm = this
      for (let i = 0; i <= 20; i++) {
        let data = {
          time: vm.randomTime(),
          price: _.random(1, 100000),
          count: _.random(1, 10),
        }
        vm.transactions.push(data)
      }
    },
    randomTime() {
      let start = new Date(2017, 0, 1)
      let end = new Date()
      let randomTime = new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()))
      var hour = randomTime.getHours()
      var minute = randomTime.getMinutes()
      var second = randomTime.getSeconds()
      var finalTime = `${hour}:${minute}:${second}`
      return finalTime
    }
  },
}
</script>

<style lang="scss">
.box-card {
  margin: 15px;
  .el-card__body {
    padding: 0;
    .el-table {
      height: 50%;
      .el-table--border {
        border: none;
      }
    }
  }
}
</style>



