<template>
  <el-table
    :data="stocksData"
    border
    header-cell-class-name="table-header-cell"
    height="500">
    <el-table-column
      fixed
      prop="name"
      label="商品名稱"
      class-name="table-first-column"
      width="150">
    </el-table-column>
    <el-table-column
      prop="longPosition"
      label="倉位多"
      width="120">
    </el-table-column>
    <el-table-column
      prop="shortPosition"
      label="倉位空"
      width="120">
    </el-table-column>
    <el-table-column
      prop="bidPrice"
      label="買進價"
      width="120">
    </el-table-column>
    <el-table-column
      prop="askPrice"
      label="買出價"
      width="120">
    </el-table-column>
    <el-table-column
      prop="strikePrice"
      label="成交價"
      width="120">
    </el-table-column>
    <el-table-column
      prop="rise"
      label="漲跌"
      width="120">
    </el-table-column>
    <el-table-column
      prop="risePercent"
      label="漲跌％"
      width="120">
    </el-table-column>
    <el-table-column
      prop="orderCount"
      label="單量"
      width="120">
    </el-table-column>
    <el-table-column
      prop="volume"
      label="總量"
      width="120">
    </el-table-column>
    <el-table-column
      prop="yesterdayClosePrice"
      label="昨收價"
      width="120">
    </el-table-column>
    <el-table-column
      prop="openPrice"
      label="開盤價"
      width="120">
    </el-table-column>
    <el-table-column
      prop="highestPrice"
      label="最高價"
      width="120">
    </el-table-column>
    <el-table-column
      prop="lowestPrice"
      label="最低價"
      width="120">
    </el-table-column>
    <el-table-column
      prop="time"
      label="時間"
      width="120">
    </el-table-column>
    <el-table-column
      prop="type"
      label="交易類型"
      width="120">
    </el-table-column>
    <el-table-column
      prop="lastClosingCost"
      label="最後成交價"
      width="120">
    </el-table-column>
    <el-table-column
      prop="lastTradingDay"
      label="最後交易日"
      width="120">
    </el-table-column>
    <el-table-column
      prop="instructions"
      label="說明"
      width="120">
      <template slot-scope="scope">
        <el-button
          size="mini"
          @click="handleEdit(scope.$index, scope.row)">說明</el-button>
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
export default {
  name: 'StocksDataTable',
  data() {
    return {
      stockDataTemplate: {
        name: '',
        longPosition: '',
        shortPosition: '',
        bidPrice: '',
        askPrice: '',
        strikePrice: '',
        rise: '',
        risePercent: '',
        orderCount: '',
        volume: '',
        yesterdayClosePrice: '',
        openPrice: '',
        highestPrice: '',
        lowestPrice: '',
        time: '',
        type: '',
        lastClosingCost: '',
        lastTradingDay: '',
        instructions: '',
      },
      products: [
        '元大台灣50',
        '元大中型100',
        '富邦科技',
        '元大電子',
        '元大台商50',
        '元大MSCI金融',
        '元大高股息',
        '富邦摩台',
        '富邦發達',
        '富邦金融',
        '元大寶滬深',
        '元大MSCI台灣',
        '永豐臺灣加權',
        '富邦上証',
        '元大上證50',
        'FH滬深',
        '富邦台50',
        '富邦上証+R',
        '元大台灣50正2',
        '元大台灣50反1',
        '富邦上証正2',
      ],
      transTypes: ['禁止新單', '停止交易', '正常交易'],
      stocksData: [],
    }
  },
  mounted() {
    let vm = this
    vm.generateFakeData()
  },
  methods: {
    generateFakeData() {
      let vm = this
      _.forEach(vm.products, function (productName) {
        let data = {
          name: productName,
          longPosition: _.random(0, 1),
          shortPosition: _.random(0, 1),
          bidPrice: _.random(1, 100000),
          askPrice: _.random(1, 100000),
          strikePrice: _.random(1, 100000),
          rise: _.random(1, 500),
          risePercent: (_.random(1, true) * 100).toFixed(2) + '%',
          orderCount: _.random(1, 5000),
          volume: _.random(1, 1000000),
          yesterdayClosePrice: _.random(1, 100000),
          openPrice: _.random(1, 100000),
          highestPrice: _.random(1, 100000),
          lowestPrice: _.random(1, 100000),
          time: vm.randomDate(),
          type: _.sample(vm.transTypes),
          lastClosingCost: _.random(1, 100000),
          lastTradingDay: vm.randomDate(),
          instructions: '',
        }
        vm.stocksData.push(data)
      })
    },
    randomDate() {
      let start = new Date(2017, 0, 1)
      let end = new Date()
      let randomDate = new Date(start.getTime() + Math.random() * (end.getTime() - start.getTime()))
      var day = randomDate.getDay()
      var month = randomDate.getMonth() + 1
      var year = randomDate.getFullYear()
      var finalDate = `${year}/${month}/${day}`
      return finalDate
    }
  }
}
</script>

<style lang="scss">
.el-table {
  .table-header-cell {
    background-color: #f5f5f5;
  }
  .table-first-column {
    background-color: #f5f5f5;
  }
}
</style>



